.app-wrapper {
    display: grid;
    max-width: 1200px;
    margin: 0 auto;
    grid-template-areas:
    "header header"
    "nav content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

body{
    background-color: silver;
}

.app-wrapper__content {
    grid-area: content;
    background-color: navajowhite;
}